.logo {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 2rem;
  font-weight: bold;
}
#logo-img {
  margin-top: 20px;
  margin-left: -50px;
}
.header {
  height: 10vh;
  margin-bottom: 20px;
}
.logo-text {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 2rem;
  margin-left: 10px;
}
.danger {
  font-size: 0.7rem;
  font-weight: 700;
  width: 15vw;
  height: 7vh;
  margin: 5px;
}
@media screen and (max-width: 600px) {
  .header {
    height: 7vh;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  #logo-img {
    width: 30px;
    height: 30px;
    margin-left: -10px;
    margin-top: 10px;

  }
  .logo-text{
    font-size: 1.2rem;
  }
  .danger {
    text-align: center;
    font-size: 0.6rem;
    font-weight: 300;
    width: 40%;
    height: 4vh;
    margin: 5px;
  }
}